export default{
state:{
        cartlist:[{
            id:1,
            title:'iphoneXs',
            price:12998,
            color:'',
            qty:1,
            kucun:10
        },{
            id:2,
            title:'Honor V20',
            price:2999,
            color:'',
            qty:1,
            kucun:20
        }]
    },
    // getterscomputed
    getters:{
      salelist(state){
        return state.cartlist.map(item=>({...item,price:item.price*0.5}));
      }
    },
    // mutationsmethods
    mutations:{
      // mutationsstate
      changeQty(state,payload){
        //statestorestate
        // payload: changeQty
        state.cartlist.forEach(item=>{
          if(item.id===payload.id){
            item.qty=payload.qty;
          }
        })
      },

       // 
      changeKucun(state,payload){
        state.cartlist.forEach(item=>{
          if(item.id===payload.id){
            item.kucun=payload.kucun;
          }
        })
      }
    },
   
    // mutations
    actions:{
      getKucun(context,payload){
        // 
        setTimeout(()=>{
          let kucun=parseInt(Math.random()*100);
          context.commit('changeKucun',{...payload,kucun});
        },1000)
      }
    }
}



